.main {
  height: 100vh;
  display: grid;
  grid-auto-rows: 120px;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  .member-avatar {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 80px;
      height: 80px;
      border-radius: 50%;
      object-fit: cover;
    }
  }
}
