.input-user {
  margin-top: 5vh;
  padding: 10px;
  background-color: #eee;
  height: 90vh;
  border-radius: 5px;
  .input-avatar {
    img {
      height: 150px;
      width: 150px;
      object-fit: cover;
    }
  }
  .right {
    .member-list {
      max-height: 80vh;
      overflow: scroll;
      .member {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .info {
          img.avatar {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            margin: 3px;
          }
        }
        .status {
          img {
            width: 30px;
            margin: 0 5px;
            border-radius: 4px;
            &.attend.done {
              background-color: aqua;
            }
            &.payment.done {
              background-color: chartreuse;
            }
          }
        }
      }
    }
  }
}
