.gift-exchange {
  background-color: #fff4;
  height: 90vh;
  margin-top: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  .pair {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px;
    .person {
      .avatar {
        width: 340px;
        height: 340px;
        border-radius: 10px;
        background-color: white;
        background-image: url('/images/unknownperson.png');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }
      .info {
        background-color: white;
        font-size: 24px;
        text-align: center;
        margin-top: 10px;
        border-radius: 5px;
        height: 72px;
      }
    }
    .exchange {
      width: 200px;
    }
  }
  .start-btn {
    font-size: 22px;
    padding: 10px 30px;
  }
  .remain {
    position: fixed;
    color: #fff3;
    bottom: 5px;
    right: 5px;
  }
}