.welcome {
  height: 100vh;
  padding-top: 100px;
  .welcome-member {
    padding-bottom: 100%;
    position: relative;
    img.avatar {
      position: absolute;
      top: 0;
      right: 50%;
      transform: translateX(50%);
      width: 70%;
      height: 70%;
      border-radius: 40px;
      object-fit: cover;
      margin: auto;
    }
  }
  .slick-current {
    img.avatar {
      width: 90%;
      height: 90%;
    }
  }
}
